import React from 'react'
import {NavLink, Outlet} from 'react-router-dom'



export default function (props) {

	const {name, img} = props

	return (
		<>
			<button style={{border: 'solid 1px #000', borderRadius: '0.5em'}}
			        onClick={() => alert(name)}
			>
				<img src={img} style={{width: '4em'}} />
				{name}
			</button>
			<NavLink to={`/meizu/run/${name}`}>{name}</NavLink>
		</>
	)

}